<template>
  <div>
    <div class="">
      <!-- <label for="input-file" class="upload-file-container">Import file HERE</label> -->
      <input class="" type="file" name="" id="input-file" @change="readFile">
    </div>
    <div class="btn-container">
      <!-- <button @click="readFile">Read</button> -->
      <button @click="importXMLfile" :disabled="workbook === ''">Transform</button>
      <button @click="downloadFile" :disabled="!checkFileType">Download</button>
      <a class="hidden" :href="file" target="_blank" :download="fileName">Download</a>
    </div>
  </div>
</template>

<script>
const XLSX = require("xlsx");

export default {
  name: 'Main',
  data() {
    return {
      // Importing excel file data
      workbook: "",
      worksheet: "",
      file: "",
      fileName: "test",
    }
  },
  computed: {
    checkFileType() {
      if(typeof this.file !== typeof "") return false;
      return this.file.includes("blob:");
    },
  },
  methods: {
    readFile() {
      const input = document.querySelector("#input-file");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        // Parse data
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        this.workbook = wb;
      }

      reader.readAsBinaryString(this.file);
    },
    getData() {
      const sheetName = this.workbook.Sheets;
      const sheet = this.workbook.Sheets[Object.keys(sheetName)[0]];
      const data = XLSX.utils.sheet_to_json(sheet);
      return data;
    },
    transformData(data) {
      let xmlData = "";
      if(!Object.keys(data[0]).includes("name")) xmlData += `<name></name>`;
      Object.keys(data[0]).forEach(k => {
        if([
          "name", "orientation", "format", "acType", "customization", "effectivity"
        ].includes(k)) xmlData += `<${k}>${data[0][k]}</${k}>`;
      });
      xmlData += `<Tasks>`;
      Object.keys(data).forEach(k => {
        xmlData += `<Task>`;
        xmlData += `<dmId>${data[k]["dmId"]}</dmId>`;
        xmlData += `<productKey>${data[k]["productKey"]}</productKey>`;
        xmlData += `<doctype>${data[k]["doctype"]}</doctype>`;
        xmlData += `<reference>${data[k]["reference"]}</reference>`;
        xmlData += `<title>${data[k]["title"]}</title>`;
        xmlData += `</Task>`;
      });
      xmlData += `</Tasks>`;
      return xmlData;
    },
    importXMLfile() {
      const data = this.getData();
      const xmlData = this.transformData(data);
      const xmlString = "<?xml version=\"1.0\" encoding=\"utf-8\"?>" + "<JobCardBasket>" + xmlData + "</JobCardBasket>";
      var blob = new Blob([xmlString], {type: "text/xml"});
      this.file = URL.createObjectURL(blob);
    },
    downloadFile() {
      const el = document.querySelector("a");
      el.classList.remove("hidden");
      el.click();
      el.classList.add("hidden");
    }
  },
  watch: {
  }
}
</script>

<style scoped>
.hidden {
  display: none;
}

.input-container {
  font-family: Arial, Helvetica, sans-serif;
  width: 200px;
